<template>
  <div id="capture-quize">
    <head-top title="截屏提问"></head-top>
    <div class="main">
      <div class="top">
        <div class="tt">
          <span
            class="info fL"
          >{{questionInfo['SSSerial']}}.[{{getQuestionTypeName(questionInfo['SSType'])}}] {{questionInfo['SSScore']}}分</span>
          <span :class="questionPattern=== 3 ? 'red':'green'">{{getPatternName(questionPattern)}}</span>
          <span class="time fR">计时 ：{{questionInfo['SSTime']}}分钟</span>
        </div>
        <div class="bg">
          <img :src="img" />
        </div>
      </div>

      <my-answer
        :question-type="questionInfo['SSType']"
        :my-answer="questionOperate['SAContent']"
        :answer-type="contentType"
        :module-picture="modulePicture"
        :true-answer="questionInfo['SSAnswer']"
        :get-score="questionOperate['SAScore']"
      ></my-answer>

      <div class="survey_info">
        <van-button
          icon="success"
          type="primary"
          disabled
          v-if="questionOperate['SAMaster']===1"
        >已掌握</van-button>
        <van-button
          icon="cross"
          type="danger"
          disabled
          v-else-if="questionOperate['SAMaster']===0"
        >未掌握</van-button>

        <van-button type="warning" disabled v-else>未参与调研</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import { Button } from "vant";
import headTop from "@/components/headTop";
import MyAnswer from "./../../course/quiz/components/MyAnswer";

import {
  ContentType,
  ModulePicture,
  ScreenPattern,
  screenPatternName,
  screenQuestionTypeName
} from "@/utils/enum";
export default {
  name: "",
  components: { headTop, MyAnswer, [Button.name]: Button },
  props: {},
  data() {
    return {
      questionID: this.$route.params.id,
      questionInfo: {},
      questionPattern: ScreenPattern.all, // 截屏提问模式 1全员作答,2随机挑人,3抢答模式
      questionOperate: {},
      img: "",
      currentIndex: 3,
      contentType: ContentType.Text, //1文本 2图片
      contentTypeDefault: ContentType.Text,
      modulePicture: ModulePicture.ScreenAnswer
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.getQuestionData();
    });
  },
  methods: {
    getQuestionData() {
      this.questionInfo = {};
      this.questionOperate = {};
      this.$mainHttp.screenQustion
        .getData({
          ssid: this.questionID,
          studentID: this.$store.getters.studentID
        })
        .then(res => {
          if (res.Result && res.Keys && res.Keys.length > 0) {
            this.questionInfo = res.Keys[0];
            this.questionOperate = res.Values[0] || {};
            this.contentType =
              this.questionOperate["SAForm"] || this.contentTypeDefault;
            this.questionPattern =
              this.questionInfo["SSPattern"] || ScreenPattern.all;

            this.img = this.$mainHttp.resourse.picture(
              this.questionInfo["SSContent"],
              ModulePicture.ScreenSubject
            );
          }
        })
        .catch(err => {});
    },
    getPatternName(val) {
      return screenPatternName(val);
    },
    getQuestionTypeName(val) {
      return screenQuestionTypeName(val);
    }
  }
};
</script>

<style lang='less' scoped>
#capture-quize {
  width: 100%;
  height: 100%;
  .main {
    background: rgba(255, 255, 255, 1);
    .top {
      width: 100%;
      height: 43vh;
      .tt {
        height: 44px;
        width: 345px;
        line-height: 44px;
        margin: 0 auto;
        span {
          display: inline-block;
          color: rgba(102, 102, 102, 1);
          &:nth-child(1) {
            font-size: 1.4rem;
            font-family: MicrosoftYaHei;
            font-weight: 400;
            color: rgba(51, 51, 51, 1);
          }
          &:nth-child(2),
          &:nth-child(3) {
            font-size: 1.2rem;
            font-family: MicrosoftYaHei;
          }
        }
        .red {
          color: #e97b7b;
        }
        .green {
          color: #00cbab;
        }
      }
      .bg {
        width: 92vw;
        height: 33vh;
        background-size: cover;
        margin: 0 auto;
        img {
          max-width: 92vw;
          max-height: 33vh;
        }
      }
    }

    .datiRrea {
      width: 92%;
      // padding: 1.5rem;
      text-align: left;
      background: rgba(255, 255, 255, 1);
      .prompt {
        margin-bottom: 4vw;
      }
      padding: 4vw;
      .area {
        color: #999999;
        position: relative;
        width: 92vw;
        height: 25vh;
        textarea {
          width: 100%;
          height: 100%;
          line-height: 13px;
          background: rgba(235, 240, 239, 1);
          border-radius: 3px;
          box-sizing: border-box;
          border: none;
          padding: 10px;
        }
        i {
          position: absolute;
          left: 10px;
          bottom: 5px;
        }
        span {
          position: absolute;
          right: 10px;
          bottom: 5px;
        }
      }
    }

    .sbumit {
      width: 240px;
      height: 40px;
      background: rgba(0, 203, 171, 1);
      border-radius: 5px;
      position: absolute;
      left: 50%;
      bottom: 50px;
      margin-left: -120px;
      border: none;
    }
  }
}

.survey_info {
  display: flex;
  justify-content: center;

  width: 100vw;
  padding: 2vh 0;
  background: #ffffff;
  button {
    width: 50%;
    margin: 0 10vw;
  }
}
</style>


